import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'


const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/gostinye',
    name: 'Gostinye',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Gostinye.vue')
  },
  {
    path: '/spalni',
    name: 'Spalni',
    component: () => import('../views/Spalni.vue')
  },
  {
    path: '/kuhni',
    name: 'Kuhni',
    component: () => import('../views/Kuhni.vue')
  },
  {
    path: '/prihozhie',
    name: 'Prihozhie',
    component: () => import('../views/Prihozhie.vue')
  },
  {
    path: '/myagkaya-mebel',
    name: 'MyagkayaMebel',
    component: () => import('../views/MyagkayaMebel.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

//Прокрутка к началу страницы
router.beforeEach(function (to, from, next) { 
  setTimeout(() => {
      window.scrollTo(0, 0);
  }, 100);
  next();
});

export default router
