<template>
    <div v-if="show" class="modal-shadow" @click.self="closeModal">
        <div class="modal">
            <div class="modal-close" @click="closeModal">&#10006;</div>
            
            <div class="modal-content">
            <slot name="body">
                
                    Дефолтный контент модального окна
                
            </slot>
            </div>
            
        </div>
    </div>
</template>

<script>
    export default {
        name: "ModalWindow",
        data: function () {
            return {
                show: false
            }
        },
        methods: {
            closeModal: function () {
                this.show = false
            }
        }
    }
</script>

