            align-items: center
            justify-content: center
            position: absolute
            top: 7px
            right: 7px
            width: 30px
            height: 30px
            cursor: pointer

         

        &-title 
            color: #0971c7
        
 
        &-content 
            margin-bottom: 20px
            display: flex
            flex-flow: row wrap
            align-items: flex-start
            &__img
                width: calc(33.333% - 10px)
